.Container {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: 0.2fr repeat(2, 0.5fr) 0.3fr 0.2fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    }


    .Div1 { 
        grid-area: 1 / 1 / 2 / 6; 
        background-color: #1F8AC0;
    }
    .Div2 { 
        grid-area: 2 / 1 / 4 / 5; 
        background-color: #EFC9AF;
    }
    .Div3 { 
        grid-area: 2 / 5 / 4 / 6; 
        background-color: #EFC9AF;
    }
    .Div4 { 
        grid-area: 4 / 1 / 5 / 6; 
        background-color: #1F8AC0;
    }
    .Div5 { 
        grid-area: 5 / 1 / 6 / 6; 
        background-color: #1F8AC0;
    }


